@media only screen and (max-width:700px) and (min-width:250px){


#rNavDiv {
    width: 100%;
    display: flex;
    justify-content: space-around;
   /* border-bottom: solid 1px grey;*/
  }

  #rNavDiv .i {
    font-size: 13px;
    margin: 0 4px;
  }

    #rNavDiv #hr2, #hr3 {
    border: none;
    height: 4px;
    background-color: indigo;
    margin-top: 4px;
    width: 0%;
    transition: 1s ease;
    background-color: #495057;
  }

  #rNavDiv #hr1{
    border: none;
    height: 4px;
    background-color: indigo;
    margin-top: 4px;
    width: 100%;
    transition: 1s ease;
    background-color: #495057;
  }

  #rNavItem1{
    cursor: pointer;
  }
  #rNavItem2{
    cursor: pointer;
  }
  #rNavItem3{
    cursor: pointer;
  }
}




















