@media only screen and (max-width:700px) and (min-width:250px){

#resume {
    max-width: 100vw;
    height: auto;
    background-color:#dee2e6;
  }

  #rTitleDiv {
    text-align: center;
    padding: 20% 0;
  }

  #resumeContentDiv {
    width: 100%;
    height: 400px;
    display: flex;
    overflow: hidden;
    border-bottom:inset;
  }

  #resume button {
    padding: 10px;
    margin: 40px 0 40px 30%;
    border-radius: 5px;
    border: none;
    outline: none;
    background-color:#343a40;
    color:white;
  }


  #resume button a{
    text-decoration: none;
    color: white;
  }


}












@media only screen and (max-width:900px) and (min-width:701px){



  #resume {
    max-width: 100vw;
    height: auto;
    background-color:#dee2e6;
    padding:10% 0;
    box-sizing: border-box;
  }

  #rTitleDiv {
    text-align: center;
    padding-bottom: 10%;
  }

  #resumeContentDiv {
    width: 80%;
    height: 400px;
    display: flex;
    overflow: hidden;
    margin: auto;
    border-bottom:inset;
  }

  #resume button {
    padding: 10px;
    margin:50px 45%;
    border-radius: 5px;
    border: none;
    outline: none;
    background-color:#343a40;
    color:white;
  }

  #resume button a{
    text-decoration: none;
    color: white;
  }
}














