#rSkills{
    min-width:100%;
    height:400px;
    background-color:#ced4da;
    /* padding-top:10px; */
    box-sizing:border-box;
  }
  
  .skill{
    width:70%;
    height:auto;
    margin:18px 0px 10px 10%;
    padding:10px;
  }
  
 .ist{
 }
  
  .skillFlex{
    display:flex;
  }
  .skillBullet{
    width:12px;
    height:12px;
    border-radius:100%;
    background-color:#343a40;
    margin-top:6px;
  }
  .language{
    margin-left:10px;
    color:#343a40;
    font-weight:500;
    font-size: 15px;
  }
  .skillScale{
    width:100%;
    height:6px;
    background-color:#adb5bd;
    border-radius:50px;
    margin-left:25px;
    margin-top:10px;
  }
  .skill-amount{
    width:60%;
    height:100%;
    background-color:#6c757d;
    border-radius:50px;
  }

  .html{
    width: 80%;
  }
  .css{
    width: 50%;
  }
  .node{
    width: 80%;
  }
  .react{
    width: 70%;
  }
  .next{
    width:80%;
  }