@media only screen and (max-width:600px) and (min-width:250px){

 #hIconDiv2{
    display: none;
  }

#headerSec{
    max-width:100vw;
    height:80vh;
    background-color:#f8f9fa;
   /* background-color:green ; */
    display:flex;
    position:relative;
    flex-direction:column-reverse;
  }
  
  #HtextDiv{
    width:100%;
    min-height:50%;

  }
  
  #hText{
    margin-top:15%;
    padding:35px;
  }
  #hText h1{
    font-size:25px;
    font-family:Helvetica ;
    font-weight: 500;
  }
  
  #hText #Hp1{
    margin:10px 0px;
    font-size:15px;
  }
  
  #hText #Hp2{
    font-size:16px;
    margin-bottom: 12px;
    
  }
  
  #hText button{
    width: 110px;
    padding:10px;
    border-radius:5px;
    background-color:#495057;
    border:none;
    outline:none;
    color:white;
    font-family: 'Poppins', sans-serif;
  }
  
  #HimgDiv{
    width:100%;
    min-height:50%;
    display: flex;
  }
  
  #hIconDiv{
    flex:2;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  
  }

  #hIconDiv #hIconSubDiv{
    display: flex;
    width: 100%;
    flex-direction: column;
    box-sizing: border-box;
  }

  #hIconDiv #hIconSubDiv svg{
    font-size: 23px;
    margin: 25px 0px 0px 0%;
    color: #343a40;
   
  }

  #HimgParent{
    flex: 8;
  }

  #hImg{
    width:90%;
    height:80%;
    background-color:#495055;
    margin:10% auto auto 0px;
    border-radius:100%;
  }
  #hImg img{
    object-fit:cover;
    width:100%;
    height:110%;
    border-radius:100%;
    position:relative;
    bottom:9%;
  }


  #shape1{
    background-color: #f8f9fa;
    width: 100vw;
    height: 100px;
  }

  #shape1 div{
    clip-path: polygon(0 71%, 100% 50%, 100% 100%, 0 100%);
    background-color: #e9ecef;
    width: 100%;
    height: 100%;
  }

  #HresumeButton a{
    color: white;
    font-size: 16px;
    text-decoration: none;
  }
}























