@media only screen and (max-width:600px) and (min-width:250px){

#contactMe{
    max-width:100vw;
    height:100vh;
    background-color:#f8f9fa;
     background-size:cover; 
    padding-top: 20px;
  }

  #msgSent{
    opacity: 100%;
    width: fit-content;
    padding: 25px;
    border-radius: 15px;
    transition: all 2s ease;
    margin: 0 auto;
    text-align: center;
    background-color: #dee2e6;
    margin-bottom: 40px;
    border: solid 1px green;
  }

  #msgSent p{
    font-size: 20px;
  }
  
  #contactMeTitle h1{
    text-align:center;
  }
  #contactMeForm{
    width:100%;
    padding:40px 0px;
  
  }
  #cForm{
    width:90%;
   background-color:#dee2e6;
   
    margin:auto;
    padding:20px;
    border-radius: 10px;
    box-sizing: border-box;
    box-shadow:  18px 18px 46px #bdc0c4,
         -18px -18px 46px #ffffff;
  }
  #cForm #leaveAmsgTxt{
    font-size:20px;
    font-weight: 500;
    margin-right: 10px;
    margin-left: 15%;
  
  }
  
  #cForm form{
    padding-top:40px;
    box-sizing: border-box;
  }
  #cForm form label{
    font-size:13px;
  }
  #cForm form input{
    margin-bottom:25px;
    margin-top:5px;
    width:100%;
    height:40px;
    outline:none;
    border:none;
    padding:10px;
    border-radius:5px;
    box-sizing: border-box;
  }
  #cForm form textarea{
    margin-top:5px;
    width:100%;
    height:100px;
    resize: none;
    outline:none;
    border: none;
    padding:5px;
    border-radius:5px;
    box-sizing: border-box;
  }
  
  #Sendbtn{
    width: 100%;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 50px 0;
}
#Sendbtn button{
  width: 100px;
  height: 40px;
  padding:10px;
  border-radius: 10px;
  border: none;
  background-color:#495057;
  outline:none;
  color:white;
  font-family: 'Poppins', sans-serif;
}

#footer{
  width: 100%;
  height: 40px;
  color: white;
  background-color: #212529;
  text-align: center;
  padding-top: 13px;
}

}





















