
@media only screen and (max-width:700px) and (min-width:250px){

#aboutMe{
    text-align:center;
    max-width:100vw;
    background-color:#e9ecef;
    padding-top: 20%;
    padding-bottom: 10%;
    box-sizing:border-box;
  }
  
  #aboutMeTitle{
    
  } 
  
  #aboutMeContent{
    margin-top:10%;
  }
  
  #abImg{
    margin-bottom:30px;
    min-height:200px;
  }
  
  #abImg img{
    width:60%;
    border-radius:20px;
  }
  
  #abText{
    padding:10px 30px;
  }
  #abExperience{
    display:flex;
    margin-top:30px;
    justify-content:space-between;
    padding:0px 20px;
  }
  #abExperience div{
    
  }
  
  #abExperience div .abExtime{
    font-size:30px;
    font-weight:500;
  }
  
  #abExperience .AbexTxt{
    font-size:13px;
  }
  
  #abDwnCV{
    padding:10px 20px;
    border-radius :5px;
    margin-top:50px;
    background-color:#495057;
    border: none;
    outline:none;
    color:white;
  }

  #abDwnCV a{
    text-decoration: none;
    color: white;
  }

}

















