@media only screen and (max-width:600px) and (min-width:250px){
  
  #navBar{
    max-width:100vw;
    position:relative;
    overflow: hidden;
  }
  
  #nBarLogo{
    width:100%;
    height:60px;
    background-color:#f8f9fa;
    box-sizing: border-box;
    display:flex;
    justify-content:space-between;
    align-items:center;
    padding-left:10%;
    border-bottom:solid 1px #e9ecef;
  }
  
  #nBarlogoText{
    font-size :30px;
    font-weight:800;
    color:#343a40;
  }
  
  #showNav{
    display:none;
  }
  
  #showNavLabel{
    width:30px;
    height:30px;
    margin-right:5%;
  }
  
 .Nlayer{
   width:100%;
   height:5px;
   background-color:#343140;
   margin:4px 0px;
   border-radius:10px;
 }
  #nNav{
    width:100%;
    position:relative;
    background-color:lightgray;
    height:0px;
    display:flex;
    flex-direction:column;
    align-items:center;
    transition:all 0s ease;
  }
  #nNav a{
    padding:15px;
    text-decoration: none;
    color:#343a40;
  }
  
  #nBarlogoText small{
    font-size: 16px;
  }

}











@media only screen and (max-width:4000px) and (min-width:601px){
  
  #navBar{
    max-width:100vw;
    height:60px;
    position:relative;
    background-color:#f8f9fa;
    border-bottom:solid 1px #e9ecef;
    display: flex;
    justify-content: space-around;
    padding: 0px auto;
    box-sizing: border-box;
  }
  
  #nBarLogo{
    height: 100%;
    width: 40%;
    box-sizing: border-box;
    /* background-color: yellowgreen; */
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: right;
    /* margin-left: 10%; */
    /* margin-right: -15%; */
    padding-right: 15%;
  }
  
  #nBarlogoText{
    font-size :30px;
    font-weight:800;
    color:#343a40;
  }
  
  #showNav{
    display:none;
  }
  
  #showNavLabel{
    width:30px;
    height:30px;
    margin-right:5%;
    display: none;
  }
  
 
  #nNav{
    height: 100%;
    width: 60%;
    position:relative;
    /* background-color:lightgray; */
    display:flex;
    align-items: center;
    justify-content: right;
    align-items:center;
    transition:all 0s ease;
    box-sizing: border-box;
    padding-right: 5%;
  }
  #nNav a{
    padding:15px;
    text-decoration: none;
    color:#343a40;
  }
  

  #nBarlogoText small{
    font-size: 16px;
  }
  
  

}

