@media only screen and (max-width:750px) and (min-width:250px){


#projects{
    max-width:100vw;
    height:auto;
    background-color:#e9ecef;
    text-align:center;
    padding:50px 0;
  }
  
  #myProjects{
    width:100%;
  }
  
 .project{
    width:300px;
    /* width: 80vw; */
    height:260px;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    margin:30px auto 30px auto;
    border-bottom:solid 5px #ced4da;

  }
 .logoNlink{
   width:100%;
   height:10%;
   display:flex;
   justify-content:space-between;
   background-color:#ced4da;
   border-top-left-radius: 10px;
   border-top-right-radius: 10px;
   padding:0px 10px 0px 5px;
   position :sticky;
   box-sizing: border-box;
 }
.browserLogo{
  display:flex;
  width:fit-content;
  height:100%;
  padding-top:9px;
  cursor: pointer;
}
.bBullet{
 width:8px;
 height:8px;
 border-radius:100px;
 background-color:grey;
 margin:1px;
}

.projLink{
padding-top:0px;
}
.projLink a{
font-size:13px;
text-decoration:none;
color: black;
}

.projImg{
height:90%;
width:100%;
overflow:hidden;
outline: hidden;
position: relative;
}

.projImg img{
width:100%;
transition:3s ease;
outline: none;
}

.projInfo{
  height:100%;
  width:100%;
  overflow:hidden;
  outline: hidden;
  position: absolute;
  top: 0px;
  color: white;
  padding-top: 20%;
  background-color: rgba(0, 0, 0, 80%);
  display: none;
  }

.projImg img:hover{
transform:translateY(-84%);
}

.proj3{

}


}

















